:root {
  font-size: 16px;
}

@media only screen and (max-width: 48em) {
  :root {
    font-size: 14px;
  }
}

#root {
  height: 100vh;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* div element created by Router */
#root > div {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
}
